import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
  export default 
  {
      data() {
      return {
          balance:123123312,
		  info_form:
          {
              transfer_id:'',
              transfer_money:''
              
          }	
    }
      },
    methods:
    {
        //确认转账
    	confirm(){
			var that = this;
            if(parseFloat(this.info_form.transfer_money)>parseFloat(this.balance))
            {
                MessageBox.alert('转让金额不可大于您的余额', '提示');
                return;

            }
			if(this.info_form.transfer_id == undefined || this.info_form.transfer_id<=0 ||this.info_form.transfer_id.length==0)
            {
                MessageBox.alert('转让id不可为空', '提示');
                return;

            }
            if(this.info_form.transfer_money == undefined || this.info_form.transfer_money<=0 || this.info_form.length==0)
            {
                MessageBox.alert('转让金额不可低于0元', '提示');
                return;

            }
			$http.get('finance.balance.transfer', { recipient:this.info_form.transfer_id , transfer_money: this.info_form.transfer_money }, '...').then(function (response) {

				if (response.result == 1) {
                    MessageBox.alert(response.msg, '提示');
					that.$router.go(-1);
					

				}else
                {
                    MessageBox.alert(response.msg, '提示');
                }
			}, function (response) {
				// error callback
			});

		}
    },
     activated () 
    {
        this.balance = this.$route.params.balance;
    },
    components: { cTitle }  
   }  


